@font-face {
  font-family: "Shink";
  src: local("Shink"), url(/WeddingWebsite/static/media/Shink.ae6c01c6.otf) format("opentype");
}

.cover-photo-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .cover-photo-container .photo {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  .cover-photo-container .overlay {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute; }
  .cover-photo-container .content {
    display: flex;
    flex-direction: column;
    position: absolute; }
    .cover-photo-container .content .title {
      color: white;
      font-weight: bolder;
      font-family: "Shink", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
      font-size: 90px;
      text-align: center;
      z-index: 2;
      line-height: 1.3em; }
    .cover-photo-container .content .subtitle {
      color: white;
      font-family: "Tahoma", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
      font-size: 18px;
      text-align: center;
      z-index: 2;
      letter-spacing: 10px;
      text-transform: uppercase; }

.nav-wrapper ul li {
  background-color: white; }
  .nav-wrapper ul li.active {
    border-bottom: #b58700 5px solid;
    height: 40px; }
  .nav-wrapper ul li:hover {
    background-color: #e9e6db; }
  .nav-wrapper ul li a {
    color: #b58700;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 2px; }

.homepage .nav-wrapper {
  margin-top: -40px; }

